.selected-car {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.selected-car__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-right: 50px;
}

.selected-car__image {
  width: 256px;
  height: auto;
}

.info__title {
  margin: 0;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  color: #121212;
}

.info__number {
  padding: 4px 8px;
  width: 100px;
  border: 1px solid #999999;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
  line-height: 16px;
  color: #121212;
  margin: 12px 0;
}

.selected-car__info .info__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 8px;
}

.selected-car__info .info__item h3 {
  margin: 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: #121212;
  margin-right: 5px;
}

.selected-car__info .info__item span {
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  color: #121212;
}

@media (max-width: 520px) {
   .selected-car {
     flex-direction: column;
   }

   .selected-car__image {
     margin-right: auto;
     margin-left: auto;
   }
}
