.languageSelector {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 48px;
  height: 48px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 13px;
  line-height: 15px;
  color: #0ec261;
  border: 1px solid;
  border-color: #111518;
  border-radius: 50%;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.languageSelector:hover {
  border-color: #eeeeee;
  color: #eeeeee;
}

.languageSelector:active {
  color: #0ec261;
}
